@import 
'../../../styles/utils/variables',
'../../../styles/utils/mixins';

.wrapper {
  background: $white;
  box-shadow: 4px 2px 15px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  border: 1px solid #A1C0E0;

  z-index: 3;
  
  width: 370px;

  @include respond-to('large') {
    width: 345px;
  }

  @include respond-to('medium') {
    width: 310px;
  }

  @include darkTheme {
    background: $black-blue;
  }
}

.header {
  width: 84%;
  margin: 20px 20px 10px 20px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  @include respond-to('large') {
    margin: 20px 20px 10px 25px;
  }

  @include respond-to('medium') {
    margin: 20px 20px 10px 20px;
  }
}

.headerLabel {
  color: #0F3971;
  font-size: 18px;

  @include respond-to('large') {
    font-size: 15px;
  }

  @include respond-to('medium') {
    font-size: 13px;
  }

  @include darkTheme {
    color: $grey;
  }
}

.close {
  $size: 18px;
  width: $size;
  height: $size;

  cursor: pointer;

  @include respond-to('large') {
    $size: 16px;
    width: $size;
    height: $size;
  }

  @include respond-to('medium') {
    $size: 12px;
    width: $size;
    height: $size;
  }
}

.pickerContainer {
  $indent: 20px;
  margin-left: $indent;
  margin-right: $indent;

  @include respond-to('large') {
    $indent: 25px;
    margin-left: $indent;
    margin-right: $indent;
  }

  @include respond-to('medium') {
    $indent: 20px;
    margin-left: $indent;
    margin-right: $indent;
  }
}

.buttons {
  $indent: 30px;
  margin-left: $indent;
  margin-right: $indent;

  margin-top: 40px;
  margin-bottom: 20px;
  width: 85%;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  @include respond-to('large') {
    $indent: 25px;
    margin-left: $indent;
    margin-right: $indent;
    margin-top: $indent;
  }

  @include respond-to('medium') {
    $indent: 20px;
    margin-left: $indent;
    margin-right: $indent;
    margin-top: $indent;
  }
}

.button {
  width: 150px;
  height: 35px;

  font-size: 15px;
  color: $white;

  @include respond-to('large') {
    width: 130px;
    height: 30px;
    font-size: 13px;
  }

  @include respond-to('medium') {
    width: 105px;
    height: 25px;
    font-size: 11px;
  }
}

.pickerContainer {
  margin-top: 18px;
}

.picker {
  margin-top: 14px;

  @include respond-to('medium') {
   margin-top: 13px; 
  }
}

.divTooltip {
  font-weight: normal;
  min-width: 300px;
  max-width: 300px;

  p {
    margin-top: 0.5em;
    margin-bottom: 1em;
  }
}